#head {
  text-align: center;
  background-color: #f0f0f0;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

#head h1 {
  margin-bottom: 10px;
  font-size: 24px;
}

#head form {
  align-items: center;
  margin-top: 20px;
  display: flex;
}

#head input[type="text"], #head select {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

#head button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 20px;
  font-size: 16px;
}

#head button:hover {
  background-color: #0056b3;
}
